<!-- MovieInfiniteScroll.vue -->
<template>
  <div class="movie-grid" ref="gridContainer">
    <div :class="['grid-container', currentView]">
      <div v-for="(movieGroup, i) in visibleMovieGroups" :key="i"
           :class="['movie-row', { 'full': movieGroup.length === rowSize }]">
        <div v-for="movie in movieGroup" :key="movie.id"
             class="movie-card"
             @mouseup="toggleWishlist(movie)">
          <img :src="getImageUrl(movie.poster_path)"
               :alt="movie.title"
               loading="lazy">
          <div class="movie-title">{{ movie.title }}</div>
          <div v-if="isInWishlist(movie.id)" class="wishlist-indicator">üëç</div>
        </div>
      </div>
    </div>

    <div ref="loadingTrigger" class="loading-trigger">
      <div v-if="isLoading" class="loading-indicator">
        <div class="spinner"></div>
        <span>Loading...</span>
      </div>
    </div>

    <button v-if="showTopButton"
            @click="scrollToTopAndReset"
            class="top-button"
            aria-label="Îß® ÏúÑÎ°ú Ïù¥Îèô">
      Top
    </button>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted, watch } from 'vue';
import axios from 'axios';
import { useWishlistService } from '@/components/home/wishlist/home-wishlist.vue';

export default {
  name: 'MovieInfiniteScroll',
  props: {
    genreCode: {
      type: String,
      required: true
    },
    apiKey: {
      type: String,
      required: true
    },
    sortingOrder: {
      type: String,
      default: 'all'
    },
    voteEverage: {
      type: Number,
      default: 100
    }
  },
  setup(props) {
    const gridContainer = ref(null);
    const loadingTrigger = ref(null);
    const movies = ref([]);
    const currentPage = ref(1);
    const rowSize = ref(4);
    const isLoading = ref(false);
    const isMobile = ref(window.innerWidth <= 768);
    const currentView = ref('grid');
    const hasMore = ref(true);
    const showTopButton = ref(false);
    const wishlistTimer = ref(null);
    const observer = ref(null);

    const { isInWishlist, toggleWishlist } = useWishlistService();

    const visibleMovieGroups = computed(() => {
      return movies.value.reduce((resultArray, item, index) => {
        const groupIndex = Math.floor(index / rowSize.value);
        if (!resultArray[groupIndex]) {
          resultArray[groupIndex] = [];
        }
        resultArray[groupIndex].push(item);
        return resultArray;
      }, []);
    });

    const getImageUrl = (path) => {
      return path ? `https://image.tmdb.org/t/p/w300${path}` : '/placeholder-image.jpg';
    };

    const fetchMovies = async () => {
      if (isLoading.value || !hasMore.value) return;

      isLoading.value = true;
      try {
        const url = props.genreCode === "0"
            ? 'https://api.themoviedb.org/3/movie/popular'
            : 'https://api.themoviedb.org/3/discover/movie';

        const params = {
          api_key: props.apiKey,
          language: 'ko-KR',
          page: currentPage.value,
          per_page: 10
        };

        if (props.genreCode !== "0") {
          Object.assign(params, { with_genres: props.genreCode });
        }

        const response = await axios.get(url, { params });
        const newMovies = response.data.results;

        if (newMovies.length > 0) {
          let movieArray = [...movies.value, ...newMovies];

          if (props.sortingOrder !== 'all') {
            movieArray = movieArray.filter((movie) =>
                movie.original_language === props.sortingOrder
            );
          }

          movieArray = movieArray.filter((movie) => {
            if (props.voteEverage === -1) return true;
            if (props.voteEverage === -2) return movie.vote_average <= 4;
            return movie.vote_average >= props.voteEverage &&
                movie.vote_average < props.voteEverage + 1;
          });

          movies.value = movieArray;
          currentPage.value++;
        } else {
          hasMore.value = false;
        }
      } catch (error) {
        console.error('Error fetching movies:', error);
      } finally {
        isLoading.value = false;
      }
    };

    const handleResize = () => {
      isMobile.value = window.innerWidth <= 768;
      if (gridContainer.value) {
        const containerWidth = gridContainer.value.offsetWidth;
        const movieCardWidth = isMobile.value ? 100 : 300;
        const horizontalGap = isMobile.value ? 10 : 15;
        rowSize.value = Math.floor(containerWidth / (movieCardWidth + horizontalGap));
      }
    };

    const handleScroll = () => {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      showTopButton.value = scrollTop > 300;
    };

    const scrollToTopAndReset = () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
      resetMovies();
    };

    const resetMovies = () => {
      movies.value = [];
      currentPage.value = 1;
      hasMore.value = true;
      fetchMovies();
    };

    const setupIntersectionObserver = () => {
      observer.value = new IntersectionObserver(
          (entries) => {
            if (entries[0].isIntersecting && !isLoading.value && hasMore.value) {
              fetchMovies();
            }
          },
          { rootMargin: '100px', threshold: 0.1 }
      );

      if (loadingTrigger.value) {
        observer.value.observe(loadingTrigger.value);
      }
    };

    onMounted(() => {
      setupIntersectionObserver();
      fetchMovies();
      handleResize();
      window.addEventListener('resize', handleResize);
      window.addEventListener('scroll', handleScroll);
    });

    onUnmounted(() => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('scroll', handleScroll);
      if (observer.value) {
        observer.value.disconnect();
      }
      if (wishlistTimer.value) {
        clearTimeout(wishlistTimer.value);
      }
    });

    watch([() => props.genreCode, () => props.sortingOrder, () => props.voteEverage], () => {
      resetMovies();
    });

    return {
      gridContainer,
      loadingTrigger,
      movies,
      isLoading,
      isMobile,
      currentView,
      showTopButton,
      visibleMovieGroups,
      rowSize,
      getImageUrl,
      scrollToTopAndReset,
      toggleWishlist,
      isInWishlist
    };
  }
};
</script>

